body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  color: #111111;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: #111111;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (max-width: 767px) {
  .action {
    position: relative;
    margin: 39px 0 0 0;
    padding: 172px 55px 25px 56px;
    background-image: url("../img/gift-photo-mobile@1x.jpg"), linear-gradient(#68b738 289px, #ffffff 1px);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    text-align: center;
  }
  .action::before {
    position: absolute;
    content: "";
    top: 65px;
    right: 111px;
    width: 56px;
    height: 61px;
    background-image: url("../img/icon-gift.svg");
  }
  .action__title {
    font-family: "Arial", sans-serif;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #ffffff;
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .action {
    background-image: url("../img/gift-photo-mobile@2x.jpg"), linear-gradient(#68b738 289px, #ffffff 1px);
    background-repeat: no-repeat;
    background-size: 320px auto, auto;
    background-position: top center;
    text-align: center;
  }
}
@media (min-width: 768px) {
  .action {
    position: relative;
    margin: 67px 0 4px 0;
    padding: 74px 119px 69px 423px;
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    background-repeat: no-repeat;
  }
  .action::before {
    position: absolute;
    content: "";
    top: 65px;
    left: 91px;
    width: 56px;
    height: 61px;
    background-image: url("../img/icon-gift.svg");
  }
  .action__title {
    margin: 0;
    font-family: "Arial", sans-serif;
    font-size: 16px;
    line-height: 20px;
    text-align: left;
    color: #ffffff;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .action {
    background-image: url("../img/gift-photo-tablet@2x.jpg");
    background-size: 100% 200px;
  }
}
@media (min-width: 1300px) {
  .action {
    position: relative;
    margin: 0;
    padding: 188px 38px 43px 41px;
    background-image: url("../img/gift-photo-desktop@1x.jpg");
    background-repeat: no-repeat;
  }
  .action::before {
    position: absolute;
    content: "";
